export const listForms = `
  query ListForms($limit: Int, $nextToken: String) {
    listForms(limit: $limit, nextToken: $nextToken) {
      items {
        CategoryId
        CreatedAt
        FormId
        FormStatus
        FormTypeId
        Title
        UpdatedAt
      }
      nextToken
    }
  }
`;
export const getOffer = /* GraphQL */ `
	query GetOffer($OfferId: ID!) {
		getOffer(OfferId: $OfferId) {
			Contingencies {
				AllRemoved
				FinanceContingencyDueDate
				FinanceContingencyRemoved
				InspectionContingencyDueDate
				InspectionContingencyRemoved
				SaleOfHomeContingencyDueDate
				SaleOfHomeContingencyRemoved
			}
			Offer {
				ClosingPeriod
				FinanceContingency
				Furniture
				InspectionContingency
				OfferPrice
				SaleOfHomeContingency
			}
			CounterOffer {
				SellerCounter {
					OfferPrice
				}
				BuyerCounter {
					OfferPrice
				}
			}
			Funding {
				PurchaseType
				LoanType
				DownPayment
			}
			PropertyData {
				FullAddress
				OwnerNames
			}
			OfferId
			OfferNo
			PropertyId
			PropertyNo
			SCAR {
				file {
					path
				}
				id
				key
				name
				presignedUrl
				size
				type
			}
			SignedPurchaseAgreement {
				file {
					path
				}
				id
				key
				name
				presignedUrl
				size
				type
			}
			Step {
				allowed_actions {
					action
					form_action
					next_step_id
				}
				allowed_upload_file_types {
					allow_files_count
					allow_single_file_max_size_mb
					file_type
				}
				buyer_can_submit_offer
				display_on_advisor_portal
				display_on_seller_dashboard
				id
				messages {
					advisor
					buyer
					seller
				}
				name
				waiting_seller_to_act
			}
			StepId
			UpdatedAt
			UpdatedBy
			SubmissionFiles {
				Files {
					presignedUrl
				}
				QuestionName
			}
		}
	}
`;
export const getOffers = /* GraphQL */ `
	query GetOffers(
		$PageIndex: Int
		$PageSize: Int
		$Query: String
		$StepIds: [Int]
		$SortBy: [AWSJSON]
	) {
		getOffers(
			PageIndex: $PageIndex
			PageSize: $PageSize
			Query: $Query
			StepIds: $StepIds
			SortBy: $SortBy
		) {
			Total
			Count
			PageIndex
			PageSize
			Items {
				CreatedAt
				Funding {
					DownPayment
					LoanType
					PurchaseType
				}
				OfferId
				OfferNo
				PropertyData {
					FullAddress
					OwnerNames
				}
				Offer {
					OfferPrice
				}
				PropertyId
				PropertyNo
				Step {
					messages {
						advisor
					}
					id
				}
				CounterOffer {
					BuyerCounter {
						OfferPrice
					}
					SellerCounter {
						OfferPrice
					}
				}
			}
		}
	}
`;
export const getProperties = /* GraphQL */ `
	query GetProperties(
		$PageIndex: Int
		$PageSize: Int
		$Query: String
		$StatusIds: [Int]
		$SortBy: [AWSJSON]
	) {
		getProperties(
			PageIndex: $PageIndex
			PageSize: $PageSize
			Query: $Query
			StatusIds: $StatusIds
			SortBy: $SortBy
		) {
			Total
			Count
			PageIndex
			PageSize
			Items {
				CreatedAt
				DTPropertyId
				PropertyData {
					City
					County
					FullAddress
					OwnerNames
					State
					StreetAddress
					Zip
				}
				PropertyId
				PropertyNo
			}
		}
	}
`;

export const getPermissions = /* GraphQL */ `
	query users_Permissions {
		users_Permissions {
			CanCreateForms
			CanDeleteProperties
			CanCreateProperties
			CanManageUsers
			CanUpdateOffers
			CanUpdateForms
			CanUpdateProperties
			CanViewForms
			CanViewOffers
			CanViewProperties
		}
	}
`;
